'use client';

import { useState } from 'react';
import Image from 'next/image';

interface GameExplainModalProps {
    onClose: () => void;
    onConnectWallet: () => void;
}

const steps = [
    {
        id: 1,
        title: 'USDT(BEP-20) 지갑을 연동하세요.',
    },
    { 
        id: 2, 
        title: '서비스를 이용하기 위한 포인트를 충전합니다.', 
    },
    { 
        id: 3, 
        title: '원하는 방에 참가하여 티켓을 구매합니다.', 
    },
    { 
        id: 4, 
        title: '매 10분마다 진행되는 추첨을 기다립니다.', 
    },
    { 
        id: 5, 
        title: '당첨 시 포인트가 지급되며, 언제든지 출금 가능합니다.', 
    },
];

export default function GameExplainModal({ onClose, onConnectWallet }: GameExplainModalProps) {
    const [currentStep, setCurrentStep] = useState(0);

    const handleNext = () => {
        setCurrentStep(prev => (prev + 1) % steps.length);
    };

    const handlePrev = () => {
        setCurrentStep(prev => (prev - 1 + steps.length) % steps.length);
    };
    
    const handleConnect = () => {
        onClose();
        onConnectWallet();
    }

    const currentStepData = steps[currentStep];

    return (
        <div 
            className="fixed inset-0 bg-black bg-opacity-80 flex justify-center items-center z-50 p-4"
            onClick={onClose}
        >
            <div 
                className="bg-[#0D102D] w-full max-w-2xl rounded-lg border-2 border-[#1ACFF8] p-6 md:p-8 relative flex flex-col text-white font-sans"
                onClick={e => e.stopPropagation()}
            >
                <h1 className="text-2xl md:text-3xl font-bold text-center mb-2">게임 룰 설명</h1>
                <h2 className="text-base md:text-lg text-center text-gray-300 mb-4">참가 방법</h2>
                <div className="w-full h-px bg-cyan-400 mb-6 md:mb-8"></div>
                
                <div className="relative border-2 border-cyan-400 rounded-lg flex-grow flex flex-col items-center justify-center p-8 min-h-[300px]">
                    <div className="absolute top-0 left-0 -mt-px -ml-px bg-cyan-500 text-white px-4 py-1 rounded-tl-lg rounded-br-lg text-lg font-bold">
                        {currentStepData.id}
                    </div>
                    <div className="flex flex-col items-center justify-center h-full text-center">
                        <p className="mb-6 text-lg md:text-xl">{currentStepData.title}</p>
                        
                        {currentStepData.id === 1 && (
                            <div className="relative mt-4">
                                <button 
                                    onClick={handleConnect}
                                    className="bg-transparent border border-white rounded-full px-12 py-3 hover:bg-white hover:text-black transition-colors text-lg"
                                >
                                    지갑 연동
                                </button>
                                <Image 
                                    src="/mouse_cursor.svg"
                                    alt="cursor"
                                    width={40}
                                    height={40}
                                    className="absolute -bottom-2 -right-5 w-8 h-8 md:w-10 md:h-10"
                                />
                            </div>
                        )}
                    </div>
                </div>

                <div className="flex justify-center items-center mt-6 md:mt-8">
                    <button onClick={handlePrev} className="text-cyan-400 text-3xl p-2">
                        ◀
                    </button>
                    <div className="flex gap-3 md:gap-4 mx-4 md:mx-6">
                        {steps.map((_, index) => (
                            <div 
                                key={index}
                                onClick={() => setCurrentStep(index)}
                                className={`w-3 h-3 rounded-full cursor-pointer transition-all ${currentStep === index ? 'bg-cyan-400' : 'border-2 border-cyan-400'}`}
                            />
                        ))}
                    </div>
                    <button onClick={handleNext} className="text-cyan-400 text-3xl p-2">
                        ▶
                    </button>
                </div>
                
                <button onClick={onClose} className="absolute top-3 right-3 md:top-4 md:right-4 text-gray-400 hover:text-white text-3xl transition-colors">&times;</button>
            </div>
        </div>
    );
} 